 }\n}\n.nav-blue > li.active a{\n    color: $primary-color;\n}\n.nav-azure > li.active a{\n    color: $info-color;\n}\n.nav-green > li.active a{\n    color: $success-color;\n}\n.nav-orange > li.active a{\n    color: $warning-color;\n}\n.nav-red > li.active a{\n    color: $danger-color;\n}\n\n.nav-text{\n    margin: $margin-bottom;\n\n    > li > a{\n        font-size: $font-size-h6;\n        text-transform: uppercase;\n        padding: 3px 0;\n        text-align: left;\n        font-weight: $font-weight-semi;\n\n    }\n    > li:first-child > a{\n        padding-top: 0;\n    }\n    h4{\n        margin-top: 0;\n    }\n}\n\n.nav-text:not(.nav-stacked){\n    > li{\n        display: inline-block;\n    }\n    > li > a{\n        margin-right: 15px;\n    }\n}\n\n.tab-space {\n  padding: 20px 0 50px;\n}\n\n// Vertical navs\n\n.vertical-navs .nav-stacked .nav-link.active:before,\n.vertical-navs .nav-stacked .nav-link.active:hover:before,\n.vertical-navs .nav-stacked .nav-link.active.show:before{\n    border-right: 11px solid #F1EAE0;\n    border-top: 11px solid transparent;\n    border-bottom: 11px solid transparent;\n    content: \"\";\n    display: inline-block;\n    position: absolute;\n    right: 0;\n    bottom: 20px;\n}\n\n.vertical-navs .nav-stacked .nav-link.active:after,\n.vertical-navs .nav-stacked .nav-link.active:hover:after,\n.vertical-navs .nav-stacked .nav-link.active.show:after{\n    border-right: 11px solid $white-color;\n    border-top: 11px solid transparent;\n    border-bottom: 11px solid transparent;\n    content: \"\";\n    display: inline-block;\n    position: absolute;\n    right: -1px;\n    bottom: 20px;\n}\n#second-tabs{\n    margin-left: 20px;\n}\n\n.nav-tabs-navigation.vertical-navs {\n  border-right: 1px solid #F1EAE0;\n  font-size: 16px;\n  padding: 0 37px;\n  border-bottom: none;\n}\n",".popover{\n    font-size: $font-size-base;\n    box-shadow: 0px 10px 50px 0px rgba(0, 0, 0, 0.2);\n    border: none;\n    line-height: 1.7;\n    max-width: 240px;\n\n    &.bs-popover-top .arrow:before,\n    &.bs-popover-left .arrow:before,\n    &.bs-popover-right .arrow:before,\n    &.bs-popover-bottom .arrow:before,{\n        border-top-color: transparent;\n        border-left-color: transparent;\n        border-right-color: transparent;\n        border-bottom-color: transparent;\n    }\n\n    .popover-header{\n        color: $default-color-opacity;\n        font-size: $font-size-base;\n        text-transform: capitalize;\n        font-weight: $font-weight-semi;\n        margin: 0;\n        margin-top: 5px;\n        border: none;\n        background-color: transparent;\n    }\n\n    &:before{\n        display: none;\n    }\n\n    &.bs-tether-element-attached-top:after{\n        border-bottom-color:$white-color;\n        top: -9px;\n    }\n\n    &.popover-primary{\n        @include popover-color($primary-color, $white-color);\n    }\n\n    &.popover-info{\n        @include popover-color($info-color, $white-color);\n    }\n\n    &.popover-warning{\n        @include popover-color($warning-color, $white-color);\n    }\n\n    &.popover-danger{\n        @include popover-color($danger-color, $white-color);\n    }\n\n    &.popover-success{\n        @include popover-color($success-color, $white-color);\n    }\n}\n\n\n.tooltip{\n    &.bs-tooltip-right .arrow:before{\n        border-right-color:$white-color;\n    }\n\n    &.bs-tooltip-top .arrow:before{\n        border-top-color:$white-color;\n    }\n\n    &.bs-tooltip-bottom .arrow:before{\n        border-bottom-color:$white-color;\n    }\n\n    &.bs-tooltip-left .arrow:before{\n        border-left-color:$white-color;\n    }\n}\n\n.tooltip-inner{\n    padding: $padding-base-vertical $padding-base-horizontal;\n    min-width: 130px;\n    background-color: $white-color;\n    font-size: $font-size-base;\n    color: inherit;\n    box-shadow: 0px 5px 25px 0px rgba(0, 0, 0, 0.2);\n}\n","@mixin popover-color($color, $text-color) {\n    background-color: $color;\n\n    .popover-body{\n        color: $text-color;\n    }\n\n\n    &.bs-popover-right .arrow:after{\n        border-right-color:$color;\n    }\n\n    &.bs-popover-top .arrow:after{\n        border-top-color:$color;\n    }\n\n    &.bs-popover-bottom .arrow:after{\n        border-bottom-color:$color;\n    }\n\n    &.bs-popover-left .arrow:after{\n        border-left-color:$color;\n    }\n\n    .popover-header{\n        color: $text-color;\n        opacity: .6;\n    }\n}\n",".modal-header {\n    border-bottom: 1px solid $medium-gray;\n    padding: 20px;\n    text-align: center;\n    display: block !important;\n\n    &.no-border-header{\n        border-bottom: 0 none !important;\n        & .modal-title{\n            margin-top: 20px;\n\n        }\n    }\n    button.close{\n      &:focus {\n        outline: none;\n      }\n    }\n\n    .modal-profile {\n      width: 70px;\n      height: 70px;\n      border-radius: 50%;\n      text-align: center;\n      line-height: 6.4;\n      border: 1px solid rgba(0,0,0,.3);\n\n      i {\n        font-size: 30px;\n      }\n    }\n}\n.modal-dialog{\n    &.modal-sm,\n    &.modal-register{\n        .modal-header{\n            button.close{\n                margin-top: 0;\n            }\n        }\n    }\n}\n\n.modal-content {\n    border: 0 none;\n    border-radius: 10px;\n    box-shadow: 0 0 15px rgba(0, 0, 0, 0.15), 0 0 1px 1px rgba(0, 0, 0, 0.1);\n    .modal-header{\n        h6{\n            margin-top: 10px;\n        }\n    }\n}\n\n.modal-dialog {\n    padding-top: 60px;\n}\n.modal-body{\n    padding: 20px 50px;\n    color: #000;\n}\n.modal-footer {\n    border-top: 1px solid $medium-gray;\n    padding: 0px;\n\n    &.no-border-footer{\n        border-top: 0 none;\n    }\n}\n.modal-footer .left-side,\n.modal-footer .right-side{\n    display: inline-block;\n    text-align: center;\n    width: 50%;\n    padding: 5px;\n}\n.modal-footer .btn-link{\n    padding: 20px;\n    width: 100%;\n    margin: 0;\n}\n.modal-footer .divider{\n    background-color: $medium-gray;\n    