gin-right: 10px;\n              margin-left: 0px;\n              font-size: 12px;\n              text-align: center;\n              line-height: 20px;\n              position: relative;\n              float: left;\n              z-index: 1;\n              display: inherit;\n              color: $opacity-5;\n            }\n\n            .sidebar-normal {\n              text-transform: capitalize;\n            }\n\n            i{\n              font-size: 17px;\n              line-height: 20px;\n              width: 26px;\n            }\n          }\n      }\n  }\n\n  .navbar-minimize{\n    position: absolute;\n    right: 20px;\n    top: 2px;\n    opacity: 1;\n\n    @extend .animation-transition-general;\n  }\n  .logo-tim{\n    border-radius: 50%;\n    border: 1px solid #333;\n    display: block;\n    height: 61px;\n    width: 61px;\n    float: left;\n    overflow: hidden;\n\n    img{\n        width: 60px;\n        height: 60px;\n    }\n  }\n\n  .nav{\n      margin-top: 20px;\n      display: block;\n\n      .caret{\n          top: 14px;\n          position: absolute;\n          right: 10px;\n      }\n\n      li{\n          > a + div .nav li > a{\n              margin-top: 7px;\n          }\n\n          > a{\n              margin: 10px 15px 0;\n              color: $white-color;\n              display: block;\n              text-decoration: none;\n              position: relative;\n              text-transform: uppercase;\n              cursor: pointer;\n              font-size: 12px;\n              padding: 10px 8px;\n              line-height: 30px;\n              opacity: .7;\n          }\n\n          .nav > li > a{\n            padding: 5px 8px;\n          }\n\n          &.active > a,\n          &.active > a > i {\n            opacity: 1;\n            // color: $danger-color;\n          }\n\n          &:hover:not(.active) > a,\n          &:focus:not(.active) > a {\n              opacity: 1;\n          }\n\n          &.active > a[data-toggle=\"collapse\"] > {\n              background-color: transparent;\n              box-shadow: $box-shadow;\n          }\n\n          &.active > a[data-toggle=\"collapse\"],\n          &.active > a[data-toggle=\"collapse\"] i,\n          &.active > a[data-toggle=\"collapse\"] ~ div > ul > li.active i,\n          &.active > a[data-toggle=\"collapse\"] ~ div > ul > li.active > a {\n            color: $danger-color;\n\n            & + div .nav .active a {\n              background-color: transparent;\n              box-shadow: none;\n\n              &:after {\n                content: \"\";\n                position: absolute;\n                width: calc(100% - 5px);\n                z-index: 0;\n              }\n            }\n          }\n\n          &.active > a[data-toggle=\"collapse\"] ~ div > .nav {\n            margin-top: 0;\n          }\n      }\n\n      p{\n          margin: 0;\n          line-height: 30px;\n          position: relative;\n          display: block;\n          height: auto;\n          white-space: nowrap;\n          font-weight: 600;\n          @extend .animation-transition-general;\n      }\n\n      i{\n          font-size: 24px;\n          float: left;\n          margin-right: 12px;\n          line-height: 30px;\n          width: 34px;\n          text-align: center;\n          color: $opacity-5;\n          position: relative;\n      }\n\n      .collapse,\n      .collapsing {\n        .nav {\n          margin-top: 0;\n        }\n      }\n  }\n\n  .sidebar-background{\n      position: absolute;\n      z-index: 1;\n      height: 100%;\n      width: 100%;\n      display: block;\n      top: 0;\n      left: 0;\n      background-size: cover;\n      background-position: center center;\n\n      &:after{\n          position: absolute;\n          z-index: 3;\n          width: 100%;\n          height: 100%;\n          content: \"\";\n          display: block;\n          background: #FFFFFF;\n          opacity: 1;\n      }\n  }\n\n  .logo{\n      position: relative;\n      padding: 7px $padding-base-horizontal;\n      z-index: 4;\n\n      a.logo-mini,\n      a.logo-normal{\n          @extend .animation-transition-general;\n      }\n\n      a.logo-mini{\n          // opacity: 0;\n          float: left;\n          width: 34px;\n          text-align: center;\n          margin-left: 10px;\n          margin-right: 12px;\n      }\n\n      a.logo-normal{\n          display: block;\n          opacity: 1;\n          padding: 11px 0 8px;\n          @include transform-translate-x(0px);\n      }\n\n      &:after{\n          content: '';\n          position: absolute;\n          bottom: 0;\n          right: 15px;\n          height: 1px;\n          width: calc(100% - 30px);\n          background-color: $opacity-5;\n\n      }\n\n      p{\n          float: left;\n          font-size: 20px;\n          margin: 10px 10px;\n          color: $white-color;\n          line-height: 20px;\n          font-family: 'Muli', \"Helvetica\", Arial, sans-serif;\n      }\n\n      .simple-text{\n          text-transform: uppercase;\n          padding: $padding-base-vertical 0;\n          display: block;\n          white-space: nowrap;\n          font-size: $font-size-large;\n          color: $white-color;\n          text-decoration: none;\n          font-weight: $font-weight-normal;\n          line-height: 30px;\n          overflow: hidden;\n      }\n  }\n\n  .logo-tim{\n      border-radius: 50%;\n      border: 1px solid #333;\n      display: block;\n      height: 61px;\n      width: 61px;\n      float: left;\n      overflow: hidden;\n\n      img{\n          width: 60px;\n          height: 60px;\n      }\n  }\n\n  &:before,\n  &:after{\n      display: block;\n      content: \"\";\n      opacity: 1;\n      position: absolute;\n      width: 100%;\n      height: 100%;\n      top: 0;\n      left: 0;\n  }\n\n  &:after{\n      @include icon-gradient($default-color);\n      z-index: 3;\n  }\n\n  &[data-color=\"white\"]{\n    @include sidebar-color($white-color);\n    @include sidebar-text-color($default-color);\n  }\n  &[data-color=\"brown\"]{\n    @include sidebar-color($default-color);\n  }\n\n  // Active color changing\n\n  &[data-active-color=\"primary\"]{\n      @include sidebar-active-color($primary-color);\n  }\n  &[data-active-color=\"info\"]{\n      @include sidebar-active-color($info-color);\n  }\n  &[data-active-color=\"success\"]{\n      @include sidebar-active-color($success-color);\n  }\n  &[data-active-color=\"warning\"]{\n      @include sidebar-active-color($warning-color);\n  }\n  &[data-active-color=\"danger\"]{\n      @include sidebar-active-color($danger-color);\n  }\n\n  .user{\n      padding-bottom: 20px;\n      margin:20px auto 0;\n      position: relative;\n\n      &:after{\n          content: '';\n          position: absolute;\n          bottom: 0;\n          right: 15px;\n          height: 1px;\n          width: calc(100% - 30px);\n          background-color: rgba(255,255,255, .3);\n      }\n\n      .photo{\n          width: 34px;\n          height: 34px;\n          overflow: hidden;\n          float: left;\n          z-index: 5;\n          margin-right: 10px;\n          border-radius: 50%;\n          margin-left: 23px;\n          @extend .animation-transition-general;\n\n          img{\n              width: 100%;\n          }\n      }\n\n      a{\n          color: $white-color;\n          text-decoration: none;\n          padding: $padding-base-vertical 15px;\n          white-space: nowrap;\n          @extend .animation-transition-general;\n      }\n\n      .info{\n          > a{\n             display: block;\n             line-height: 18px;\n\n              > span{\n                 @extend .animation-transition-general;\n                 display: block;\n                 position: relative;\n                 opacity: 1;\n              }\n          }\n\n          .caret{\n              position: absolute;\n              top: 8px;\n              right: 18px;\n          }\n      }\n  }\n}\n\n.visible-on-sidebar-regular{\n  display: inline-block !important;\n}\n.visible-on-sidebar-mini{\n  display: none !important;\n}\n\n.off-canvas-sidebar{\n  .nav {\n      > li > a,\n      > li > a:hover{\n          color: $white-color;\n      }\n\n      > li > a:focus{\n          background: rgba(200, 200, 200, 0.2);\n      }\n  }\n}\n\n\n.main-panel{\n  position: relative;\n  float: right;\n  width: $sidebar-width;\n  background-color: #f4f3ef;\n\n  @include transition (0.50s, cubic-bezier(0.685, 0.0473, 0.346, 1));\n\n  .main-content{\n      padding: 0 30px 30px;\n      min-height: calc(100vh - 123px);\n      margin-top: 93px;\n  }\n\n  .navbar{\n      margin-bottom: 0;\n  }\n\n\n  .header{\n      margin-bottom: 50px;\n  }\n}\n\n\n.perfect-scrollbar-on{\n.sidebar,\n.main-panel{\n    height: 100%;\n    max-height: 100%;\n}\n}\n\n\n@media (min-width: 991px) {\n  .sidebar,\n  .main-panel,\n  .sidebar-wrapper{\n      -webkit-transition-property: top,bottom,width;\n      transition-property: top,bottom, width;\n      -webkit-transition-duration: .2s,.2s, .35s;\n      transition-duration: .2s,.2s, .35s;\n      -webkit-transition-timing-function: linear,linear,ease;\n      transition-timing-function: linear,linear,ease;\n      -webkit-overflow-scrolling: touch;\n  }\n\n\n  .sidebar-mini{\n      .visible-on-sidebar-regular{\n        display: none !important;\n      }\n      .visible-on-sidebar-mini{\n        display: inline-block !important;\n      }\n\n      .sidebar,\n      .sidebar .sidebar-wrapper{\n        width: 80px;\n      }\n\n      .main-panel{\n        width: $sidebar-mini-width;\n      }\n\n      .sidebar{\n        display: block;\n        z-index: 1030;\n\n        .logo{\n            a.logo-normal{\n                opacity: 0;\n                @include transform-translate-x(-25px);\n            }\n            a.logo-mini{\n                opacity: 1;\n            }\n        }\n\n        .sidebar-wrapper{\n          > .nav [data-toggle=\"collapse\"] ~ div > ul > li > a .sidebar-normal,\n          .user .info [data-toggle=\"collapse\"] ~ div > ul > li > a .sidebar-normal,\n          .user .info > a > span,\n          > .nav li > a p{\n              @include transform-translate-x(-25px);\n              opacity: 0;\n          }\n        }\n      }\n\n      .sidebar:hover {\n        width: 260px;\n\n        .logo {\n          a.logo-normal {\n              opacity: 1;\n              @include transform-translate-x(0);\n          }\n        }\n\n        .navbar-minimize{\n          opacity: 1;\n        }\n        .sidebar-wrapper{\n          width: 260px;\n\n          > .nav li > a p,\n          > .nav [data-toggle=\"collapse\"] ~ div > ul > li > a .sidebar-normal,\n          .user .info [data-toggle=\"collapse\"] ~ div > ul > li > a .sidebar-normal,\n          .user .info > a > span{\n            @include transform-translate-x(0px);\n            opacity: 1;\n          }\n        }\n      }\n}\n\n}\n\n.panel-header {\nheight: 260px;\npadding-top: 80px;\npadding-bottom: 45px;\nbackground: #141E30;  /* fallback for old browsers */\nbackground: -webkit-gradient(linear, left top, right top, from(#0c2646), color-stop(60%, #204065), to(#2a5788));\nbackground: linear-gradient(to right, #0c2646 0%, #204065 60%, #2a5788 100%);\nposition: relative;\noverflow: hidden;\n\n.header{\n  .title{\n    color: $white-color;\n  }\n  .category{\n    max-width: 600px;\n    color: $opacity-5;\n    margin: 0 auto;\n    font-size: 13px;\n\n    a{\n      color: $white-color;\n    }\n  }\n}\n}\n\n.panel-header-sm{\nheight: 135px;\n}\n\n.panel-header-lg{\nheight: 380px;\n}\n\n\n// Stying for logos\n\n.sidebar .logo .simple-text .logo-img{\n  display: -webkit-inline-box;\n  display: -ms-inline-flexbox;\n  display: inline-flex;\n  -webkit-box-align: center;\n  -ms-flex-align: center;\n  align-items: center;\n  -webkit-box-pack: center;\n  -ms-flex-pack: center;\n  justify-content: center;\n  height: 34px;\n  width: 34px;\n  background: #FFFFFF;\n  border-radius: 50%;\n  text-align: center;\n  overflow: hidden;\n\n  img{\n    width: 51%;\n    vertical-align: unset;\n    height: 55px;\n  }\n}\n\n.sidebar {\n&[data-color=\"white\"] {\n  border-right: 1px solid #ddd;\n}\n}\n",".card-timeline{\n    .timeline {\n        list-style: none;\n        padding: 20px 0 20px;\n        position: relative;\n\n        &:before {\n            top: 0;\n            bottom: 0;\n            position: absolute;\n            content: \" \";\n            width: 3px;\n            background-color: darken($light-gray, 3%);\n            left: 50%;\n            margin-left: -1.5px;\n        }\n\n        .timeline-footer{\n          .btn{\n            margin: 0;\n          }\n        }\n\n        h6{\n            color: $dark-gray;\n            font-weight: 400;\n            margin: 10px 0px 0px;\n        }\n\n        &.timeline-simple{\n\n            &:before{\n                left: 5%;\n            }\n\n            > li > .timeline-panel{\n                width: 86%;\n            }\n\n            > li > .timeline-badge{\n                left: 5%;\n            }\n        }\n\n        > li {\n            margin-bottom: 20px;\n            position: relative;\n\n            &:before,\n            &:after {\n                content: \" \";\n                display: table;\n            }\n\n            &:after {\n                clear: both;\n            }\n\n        }\n\n        > li > .timeline-panel {\n            width: 45%;\n            float: left;\n            padding: 20px;\n            border-radius: $border-radius-small;\n            box-shadow: 0 2px 2px rgba(204, 197, 185, 0.5);\n            background-color: $white-color;\n            color: $black-color;\n            margin-bottom: 20px;\n            position: relative;\n\n            &:before {\n                position: absolute;\n                top: 26px;\n                right: -15px;\n                display: inline-block;\n                border-top: 15px solid transparent;\n                border-left: 15px