in: 0;\n  }\n\n  span{\n    padding-left: 15px;\n    text-align: left;\n    max-width: 125px;\n  }\n\n}\n",".card .info-area{\n  padding: 40px 0 40px;\n  text-align: center;\n  position: relative;\n  z-index: 2;\n}\n",".card-lock{\n    .card-header{\n        img{\n            width: 120px;\n            height: 120px;\n            border-radius: 50%;\n            margin-top: -70px;\n        }\n    }\n\n}\n",".card-pricing{\n    text-align: center;\n\n    .card-body{\n        padding: 15px!important;\n    }\n    .card-category{\n        margin: 10px 0 10px;\n        color: $black-color;\n    }\n    .card-icon{\n        padding: 15px 0 5px;\n        transform: translateY(0%);\n        color: $black-color;\n\n        &.icon-info{\n            color: $info-color;\n        }\n        &.icon-danger{\n            color: $danger-color;\n        }\n        &.icon-primary{\n            color: $primary-color;\n        }\n        &.icon-success{\n            color: $success-color;\n        }\n        &.icon-warning{\n            color: $warning-color;\n        }\n\n        i {\n          font-size: 40px;\n          width: 105px;\n          border: 2px solid $table-line-color;\n          border-radius: 50%;\n          height: 105px;\n          line-height: 105px;\n        }\n    }\n    .card-title{\n        margin-top: 30px !important;\n    }\n    ul{\n        list-style: none;\n        padding: 0;\n        max-width: 240px;\n        margin: 20px auto;\n\n        li{\n            padding: 5px 0;\n            list-style-type: none;\n            b{\n                font-weight: 600;\n                color: $black-color;\n            }\n        }\n    }\n    .btn-neutral{\n        color: $default-color;\n\n        &:hover, :focus{\n            color: $default-states-color;\n        }\n    }\n\n    &.card-background-image{\n        ul{\n            li{\n                color: $white-color;\n                text-align: center;\n                border-color: rgba(255,255,255,.3);\n\n                b{\n                    color: $white-color !important;\n                }\n            }\n        }\n        .card-description{\n            color: $white-color !important;\n        }\n        .card-title{\n            small{\n                color: rgba(255, 255, 255, 0.6);\n            }\n        }\n    }\n}\n\n.card-pricing.card-plain{\n    .card-category,\n    .card-title{\n        color: $black-color;\n    }\n    ul{\n        li{\n            b{\n                font-weight: 600;\n                color: $black-color;\n            }\n        }\n    }\n}\n\n\n.card[data-background=\"image\"] .card-icon i,\n.card[data-background=\"color\"] .card-icon i,\n.card[data-color] .card-icon i {\n  color: $white-color;\n  border: 2px solid rgba(255, 255, 255, 0.3);\n}\n","%card-profile{\n    margin-top: 30px;\n    text-align: center;\n\n    .card-body .card-title{\n        margin-top: 0;\n    }\n\n     [class*=col-]{\n        .card-description{\n            margin-bottom: 0;\n\n            & + .card-footer{\n                margin-top: 8px;\n            }\n        }\n\n\n     }\n\n    .card-header-avatar{\n        max-width: 130px;\n        max-height: 130px;\n        margin: -60px auto 0;\n\n        img{\n            border-radius: 50% !important;\n        }\n\n        & + .card-body{\n            margin-top: 15px;\n        }\n    }\n\n    &.card-plain{\n        .card-header-avatar{\n            margin-top: 0;\n        }\n    }\n    .card-body{\n        .card-avatar{\n            margin: 0 auto 30px;\n        }\n    }\n}\n\n.card-profile{\n  @extend %card-profile;\n}\n",".card-signup{\n    .header{\n        margin-left: 20px;\n        margin-right: 20px;\n        padding: 30px 0;\n    }\n    .text-divider{\n        margin-top: 30px;\n        margin-bottom: 0px;\n        text-align: center;\n    }\n    .content{\n        padding: 0px 30px;\n    }\n\n    .form-check{\n        margin-top: 20px;\n\n        label{\n          margin-left: 7px;\n          padding-